import React from 'react';
import { SetMetadata } from '../components/Wrappers';
import { blogBaseUrl } from '../configs/SiteConfig'

export default function Metadata(props) {
  return (
      <SetMetadata>
        <link rel='shortcut icon' type='image/x-icon' href='/images/favicon.ico' />
        <title>{props.Title}</title>
        <meta property="og:title" content={props.Title} />
        <meta property="og:description" content={props.Description} />
        <meta property="og:image" content="https://cdn.bipul.in/bipul.in/images/meta.png"/>
        { props.BlogId &&
          <meta property="og:url" content={ blogBaseUrl + props.BlogId} />
        }        
        <meta property='og:type' content='article' />
        <meta property='article:author' content='https://www.facebook.com/TheBipulRaman' />
        <meta property='article:publisher' content='https://www.facebook.com/TheBipulRaman' />
        <meta property='og:section' content="Blog" />
        <meta property='og:site_name' content="Bipul's Web" />
        <meta property="twitter:title" content={props.Title} />
        <meta property="twitter:description" content={props.Description} />
        <meta property="twitter:image" content="https://cdn.bipul.in/bipul.in/images/meta.png" />
        <meta property="twitter:card" content="summary_large_image"></meta>
        <meta property="twitter:site" content="@BipulRaman" />
        <meta property="twitter:creator" content="@BipulRaman" />
      </SetMetadata>  
  );
}